<style type="text/scss" lang="scss">
  @import "../../../scss/variables";
  .page-dhcx {
    position: relative;
    background: #fff;
    ul,li{
      list-style: none;
    }
    .page-dhcx-search {
      width: 100%;
      height: .6rem;
      align-items: center;
      justify-items: center;
      background: rgba(245, 245, 245, 1);
    }
    .page-dhcx-list {
      position: fixed;
      overflow-y: auto;
      top: 0.6rem;
      bottom: .7rem;
      left: 0;
      right: 0;
      padding: 0 .125rem;
      background: #fff;
      .page-dhcx-list-title,.page-dhcx-list-item{
        color:#888;
        height:.5rem;
        line-height:.5rem;
        border-bottom:1px solid #F3F3F3;
        font-size: $font-size-littlebig;
      }
    }
  }
  .czy-search{
    flex: 1;
    height: .35rem;
    margin: 0 0.125rem !important;
    background: #fff;
    border-radius: .175rem;
    border: 1px solid #ddd;
    overflow: hidden;
    position: relative;
    .mint-searchbar{
      padding: 0;
      background:transparent;
    }
    .mint-searchbar-inner{
      border-radius: .175rem;
    }
    .mint-searchbar-core{
      text-indent: .145rem;
      font-size: .14rem;
      color: #999;
    }
    .mintui-search,.mint-searchbar-cancel{
      display: none;
    }
    .icon-yuyin{
      z-index: 2018;
      position: absolute;
      width: .12rem;
      height: .175rem;
      display: block;
      right:0.145rem;
      top: 0.085rem;
      box-sizing: border-box;
      color: #666;
    }
  }
</style>
<template>
  <div class="page-dhcx">
    <div class="page-dhcx-search am-flexbox">
      <div class="czy-search am-flexbox-item">
        <mt-search v-model="value" cancel-text="取消" placeholder="查询内容或者点击麦克风查询">
        </mt-search>
        <!--<i class="iconfont icon-yuyin"></i>-->
      </div>
    </div>
    <div class="page-dhcx-list">
      <div class="page-dhcx-list-title am-ft-12">搜索记录</div>
      <mt-loadmore :bottom-method="loadBottom" :auto-fill="false">
        <ul>
          <li v-for="(item,index) in lists" :key="index" @click.stop.prevent="turnQyxx" class="am-ft-sm page-dhcx-list-item">
              {{item.name}}
          </li>
        </ul>
      </mt-loadmore>
    </div>
  </div>
</template>
<script>
import {Search, Loadmore} from 'mint-ui';
import Vue from 'vue';

Vue.component(Loadmore.name, Loadmore);
Vue.component(Search.name, Search);

// const ERR_OK = 0;
export default {
  name: 'czy-dhcx',
  data () {
    return {
      value: '',
      pageNo: '1',
      pageSize: '10',
      lists: []
    }
  },
  created () {
    this.initPage();
  },
  watch: {
    value: (n, o) => {
      let self = this;
      if (n) {
        setTimeout(() => {
          self.initPage();
        }, 400);
      }
    }
  },
  methods: {
    initPage () {
      //      this.$http.get('../../../../static/data/dhlb.json').then((res) => {
      //        res = res.body;
      //        this.lists = res.dhlb;
      //      }).catch((msg) => {
      //        console.log(msg);
      //      });
      this.lists = [
        {'id': '1', 'name': '测试文本1'},
        {'id': '2', 'name': '测试文本2'},
        {'id': '3', 'name': '测试文本3'},
        {'id': '4', 'name': '测试文本4'},
        {'id': '5', 'name': '测试文本5'},
        {'id': '6', 'name': '测试文本6'},
        {'id': '7', 'name': '测试文本7'},
        {'id': '8', 'name': '测试文本8'},
        {'id': '9', 'name': '测试文本9'},
        {'id': '10', 'name': '测试文本10'},
        {'id': '11', 'name': '测试文本11'},
        {'id': '12', 'name': '测试文本12'},
        {'id': '13', 'name': '测试文本13'},
        {'id': '14', 'name': '测试文本14'},
        {'id': '15', 'name': '测试文本15'},
        {'id': '16', 'name': '测试文本16'},
        {'id': '17', 'name': '测试文本17'},
        {'id': '18', 'name': '测试文本18'},
        {'id': '19', 'name': '测试文本19'},
        {'id': '20', 'name': '测试文本20'},
        {'id': '21', 'name': '测试文本21'},
        {'id': '22', 'name': '测试文本22'},
        {'id': '23', 'name': '测试文本23'},
        {'id': '24', 'name': '测试文本24'},
        {'id': '25', 'name': '测试文本25'},
        {'id': '26', 'name': '测试文本26'}
      ];
    },
    loadMore () {
      setTimeout(() => {
        for (let i = 1; i <= 10; i++) {
          this.lists.push({id: this.lists.length + 1, name: '杭州孚嘉科技有限公司1234'});
        }
      }, 2500);
    },
    loadBottom () {
      this.loadMore();
    },
    turnQyxx () {
      this.$router.push({name: 'czy-dhcxList-dhcx-qyxx'})
    }
  }
}
</script>



