<style type="text/scss" lang="scss">
  .page-qywnlss {
    .page-qywnlss-list{
      li{
        padding: 0 0.15rem;
        line-height: .4rem;
        border-bottom: 1px dashed #eee;
      }
    }
  }

</style>
<template>
    <div class="page-qywnlss">
       <div class="czy-list-title">{{title}}</div>
      <div class="czy-list">
        <ul>
          <li class="am-flexbox" v-for="(item,index) in newLists" :key="index">
            <div class="list-text-left am-flexbox-item">{{item.year}}</div>
            <div class="list-text-right money am-flexbox-item am-ft-right">{{item.money}}</div>
          </li>
        </ul>
        <div class="czy-scroll-loading" @click="loadMore" v-show="more">点击加载更多</div>
      </div>
    </div>
</template>
<script>
export default {
  name: 'czy-qywnlss',
  data () {
    return {
      more: true,
      title: '',
      lists: [],
      newLists: []
    };
  },
  created () {
    this.initPage();
  },
  methods: {
    initPage () {
      //      this.$http.get('../../../../static/data/qywnlss.json').then((res) => {
      //        res = res.body;
      //        this.title = res.qywnlss.title;
      //        this.lists = res.qywnlss.lists;
      //        this.getData(this.lists);
      //      }).catch((msg) => {
      //        console.log(msg);
      //      });
      let res = {
        'qywnlss': {
          'title': '杭州孚嘉科技有限公司历年税收明细表(单位：万)',
          'lists': [
            {'id': '1', 'year': '2018', 'money': 23231.187},
            {'id': '2', 'year': '2017', 'money': 23231.187},
            {'id': '3', 'year': '2016', 'money': 23231.187},
            {'id': '4', 'year': '2015', 'money': 23231.187},
            {'id': '5', 'year': '2014', 'money': 23231.187},
            {'id': '6', 'year': '2013', 'money': 23231.187},
            {'id': '7', 'year': '2012', 'money': 23231.187},
            {'id': '8', 'year': '2011', 'money': 23231.187},
            {'id': '9', 'year': '2010', 'money': 23231.187},
            {'id': '10', 'year': '2009', 'money': 23231.187},
            {'id': '11', 'year': '2008', 'money': 23231.187}
          ]
        }
      };
      this.title = res.qywnlss.title;
      this.lists = res.qywnlss.lists;
      this.getData(this.lists);
    },
    getData (data) {
      console.log(data);
      let newList = [];
      if (data.length > 5) {
        this.more = true;
        let lists = data.slice(0, 5);
        for (let i = 0; i < lists.length; i++) {
          newList.push(lists[i]);
        }
        this.newLists = newList;
      }
    },
    loadMore () {
      this.newLists = this.lists;
      this.more = false;
    }
  }
};
</script>



