<style type="text/scss" lang="scss">
  .page-qyxx {
    background: #fff;
    .page-qyxx-header{
      padding: 0 .205rem;
      height: 1.18rem;
      border-bottom: .01rem solid #eee;
      .img{
        width: .5rem;
        height: .5rem;
        border-radius:100%;
        background: #3478F6;
        .iconfont{
          flex: 1;
          text-align: center;
          font-size: .235rem;
          color: #fff;
        }
      }
      .info{
        margin-left: .195rem;
        h3{
          color:#333;
        }
        span{
          font-size: .14rem;
          color: #888;
        }
      }
    }
  }
</style>
<template>
  <div class="page-qyxx">
    <div class="page-qyxx-header am-flexbox">
      <div class="img am-flexbox">
        <i :class="qyxx.iconCls"></i>
      </div>
      <div class="am-flexbox-item info">
        <h4>{{qyxx.name}}</h4>
        <span>税号：{{qyxx.sh}}</span>
      </div>
    </div>
    <div class="czy-list-title">明细表（单位：万）</div>
    <div class="czy-list">
      <ul>
        <li v-for='(item, index) in qyxx.mxList' :key="index" >
          <div v-if="index===0" class="am-flexbox">
            <label class="am-flexbox-item">{{item.text}}</label>
            <div class="text">{{item.value}}<i class="iconfont icon-rili" @click="openDate"></i></div>
          </div>
          <div v-else-if="index===3">
            <router-link :to="{name:'czy-dhcxList-dhcx-qywnlss'}" class="am-flexbox">
              <label class="am-flexbox-item">{{item.text}}</label>
              <i class="iconfont icon-jiantou"></i>
            </router-link>
          </div>
          <div class="am-flexbox" v-else>
            <label class="am-flexbox-item">{{item.text}}</label>
            <div class="text">{{item.value}}</div>
          </div>
        </li>
      </ul>
    </div>
    <mt-datetime-picker
      ref="picker"
      type="date"
      v-model="pickerValue">
    </mt-datetime-picker>
  </div>
</template>
<script>
import { DatetimePicker } from 'mint-ui';
import Vue from 'vue';
Vue.component(DatetimePicker.name, DatetimePicker);
export default {
  name: 'czy-dhcx-qyxx',
  data () {
    return {
      pickerValue: '',
      qyxx: {}
    }
  },
  methods: {
    openDate () {
      this.$refs.picker.open();
    },
    collect () {
      alert('收藏')
    },
    initPage () {
      //      this.$http.get('../../../../static/data/qyxx.json').then((res) => {
      //        res = res.body;
      //        this.qyxx = res.qyxx;
      //      }).catch((msg) => {
      //        console.log(msg);
      //      });
      this.qyxx = {
        'iconCls': 'iconfont icon-web-icon-',
        'name': '杭州孚嘉科技有限公司',
        'sh': '2928392324343553',
        'title': '明细表(单位：万)',
        'mxList': [
          {'text': '时间', 'value': '2018年2月'},
          {'text': '财政分片', 'value': '拱墅区'},
          {'text': '税收收入', 'value': '23.231.187'},
          {'text': '历年税收', 'value': ''}
        ]
      }
    }
  },
  created () {
    this.initPage();
    // 设置右上角按钮属性
    MyAlipayJSBridge('showOptionMenu');
    MyAlipayJSBridge('setOptionMenu', {
      title: '收藏'
    });
    MyAlipayJSBridge('optionMenu', {type: 'add'}, this.collect);
  },
  beforeRouteLeave (to, from, next) {
    MyAlipayJSBridge('optionMenu', {type: 'remove'}, this.collect);
    next();
  }
}
</script>



